import {
    Component,
    h,
    Element,
    State,
    Prop,
    Event,
    EventEmitter,
    Watch,
} from '@stencil/core';
import { fetchTranslations } from '../../../utils/translation';
import { ColDef } from 'ag-grid-community';

@Component({
    tag: 'atui-column-manager',
    shadow: false,
})
export class AtuiColumnManagerComponent {
    /**
     * Column definitions used in your atui-table
     */
    @Prop()
    col_defs: ColDef[];

    @Element() el;
    @State() translations: any;
    @State() checkedColumns: string[] = [];

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
        this.initializeCheckedColumns();
    }

    @Watch('col_defs')
    colDefsChanged() {
        this.initializeCheckedColumns();
    }

    private initializeCheckedColumns() {
        if (this.col_defs) {
            this.checkedColumns = this.col_defs
                .filter((colDef) => colDef.hide !== true)
                .map((colDef) => colDef.field);
        }
    }

    /**
     * Custom event used by atui-table-actions to perform ag-grid logic
     *
     * Emitted when checkbox is clicked
     */
    @Event({ eventName: 'atuiChange' })
    atuiChange: EventEmitter;

    changeHandler(event: Event) {
        const checkboxEl = event.target as HTMLInputElement;
        const columnId = checkboxEl.id;
        const isChecked = checkboxEl.checked;

        if (isChecked) {
            this.checkedColumns = [...this.checkedColumns, columnId];
        } else {
            this.checkedColumns = this.checkedColumns.filter(
                (id) => id !== columnId,
            );
        }

        this.atuiChange.emit({
            id: columnId,
            checked: isChecked,
        });
    }

    render() {
        return (
            <atui-menu
                autoclose={false}
                width="auto"
                align="end"
            >
                <atui-tooltip
                    slot="menu-trigger"
                    position="top"
                >
                    <atui-button
                        slot="tooltip-trigger"
                        type="secondaryText"
                        icon="graphic_eq"
                    />
                    <span slot="tooltip-content">
                        {this.translations.ATUI.TABLE.MANAGE_COLUMNS}
                    </span>
                </atui-tooltip>
                <atui-checkbox-group
                    slot="menu-content"
                    options={
                        this.col_defs
                            ? this.col_defs.map((colDef) => {
                                  return {
                                      option_id: colDef.field,
                                      label: colDef.field,
                                  };
                              })
                            : []
                    }
                    value={this.checkedColumns}
                    onChange={(event) => this.changeHandler(event)}
                />
            </atui-menu>
        );
    }
}
